export default {
	data() {
		return {
			mobile: '132****0986',
			code: '获取验证码',
			disabled: false,
			msgCode: '',
			num: 60
		}
	},
	onLoad() {

	},
	methods: {
		// 下一步input
		next() {
			if (this.msgCode == '') {
				Toast.fail('请输入验证码');
			} else {
				uni.navigateTo({
					url: '../set/bindmobile',
				});
			}

		},
		// 获取验证码
		getCode() {
			if (this.code == '获取验证码' || this.code == '重新发送') {
				this.disabled = true;
				this.code = this.num + 's';
				var timer = setInterval(() => {
					this.num--;
					this.code = this.num + 's';
					if (this.num == 0) {
						clearInterval(timer);
						this.code = '重新发送';
						this.num = 60;
						this.disabled = false;
					}
				}, 1000)
			}
		}
	},
}
